.app-wrapper {
  display: grid;
  grid-template-rows: min-content 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
    "h h"
    "n c";

  /* grid-gap: 10px; */
  width: 100%;
  margin: 0 auto;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(189 225 251);
}
